<template>
  <div class="certification box">
    <div class="list flex space_between">
      <div class="list-l font-bold">选择证件</div>
      <div class="list-r flex" @click.stop='pickerShow'>
        <div>居民身份证</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div class="upload">
      <div class='list noBorder tl'>上传照片</div>
      <div class="flex upload-img space_between tc">
        <div  @click.stop='openUploadDialog(1,"",tempCardType,false,getUploadMesApp1)' :key="('img1_'+uploadImg1.localImg)" class="upload-img1 bgCss" :style="{'background-image':'url('+(showUpload1 ? uploadImg1.localImg : upload1)+')'}">
          <div class="no-upload" v-if="!uploadImg1.res">
            <img src="../../assets/img/addCar/add.png"/>
            <div>扫描人像面</div>
          </div>
          <div v-else class="reset-upload">重新上传</div>
        </div>
        <div @click.stop='openUploadDialog(2,"",tempCardType,tempCardType==0 ? false:true,getUploadMesApp2)' :key="('img2_'+uploadImg2.localImg)" class="upload-img2 bgCss" :style="{'background-image':'url('+(showUpload2 ? uploadImg2.localImg : upload2)+')'}">
          <div class="no-upload" v-if="!uploadImg2.res">
            <img src="../../assets/img/addCar/add.png"/>
            <div>扫描国徽面</div>
          </div>
          <div v-else class="reset-upload">重新上传</div>
        </div>
      </div>
    </div>
    <div class="upload-mes">
      <div v-show='showUpload1' class="flex space_between">
        <div class="upload-mes-l">真实姓名</div>
        <input @input="nameLen" v-model='uploadJson.name' placeholder="请输入姓名" class="upload-mes-r item-input tr"/>
      </div>
      <div v-show='showUpload1' class="flex space_between">
        <div class="upload-mes-l">证件号</div>
        <input type='text' @input="authNumberLen" v-model='uploadJson.authNumber' placeholder="请输入证件号" class="upload-mes-r item-input tr"/>
      </div>
      <div v-show='showUpload1' class="flex space_between">
        <div class="upload-mes-l">地址</div>
        <input type='text' v-model='uploadJson.address' placeholder="请输入身份证地址" class="upload-mes-r item-input tr"/>
      </div>
      <div v-show='showUpload2' class="flex space_between">
        <div class="upload-mes-l">证件起始日期</div>
        <div class="upload-mes-r flex" @click.stop="dateDialogShow('0')">
          <div>{{startTime ? startTime : "请选择"}}</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
      <div v-show='showUpload2' class="flex space_between">
        <div class="upload-mes-l">长期证件</div>
        <img v-if="!isLong" @click.stop='isLong=true;' class="time-status" src="../../assets/img/face/tine-close.png"/>
        <img v-else @click.stop='isLong=false;' class="time-status" src="../../assets/img/face/time-open.png"/>
      </div>
      <div v-show='showUpload2' v-if='!isLong' class="flex space_between">
        <div class="upload-mes-l">证件截止日期</div>
       <div class="upload-mes-r flex" @click.stop="dateDialogShow('1')">
         <div>{{endTime ? endTime:"请选择"}}</div>
         <img src="../../assets/img/addCar/click.png"/>
       </div>
      </div>
    </div>
    <div class="submit tc phone-pad">
      <div class="submit-text">信息仅用于实名认证，我们将严格保护您的数据和隐私安全</div>
      <div @click.stop="startBtn" class="btn font-bold">
        <!-- <div :class="hasColBtn ? 'check':''">{{uploadJson.imageId ? '提交':'开始刷脸'}}</div> -->
        <div :class="hasColBtn ? 'check':''">开始刷脸</div>
        <!--测试-->
        <!-- <div @click.stop='gotoRouter("writeMes")'>启用人工审核</div> -->
      </div>
    </div>
    <!--切换护照或者往来通行证-->
    <div v-if="errorShow" class="credentials-bg hiddenbg tc">
      <div class="credentials-bg-main">
        <div class="credentials-bg-top">
          <div v-if="errorType==1" class="text1">是否确认切换为护照</div>
          <div v-if="errorType==2" class="text1">是否确认切换为<br/>{{tempCardType==2 ? '台湾居民':'港澳居民'}}往来通行证</div>
          <div v-if="errorType==1 || errorType==2" class="text2">您选择的证件必须进行人工审核</div>
          <div v-if="errorType==11" class="text1">识别失败</div>
          <div v-if="errorType==11" class="text2 tl">
            1.请保证图片焦点清晰，信息可读,不可太小或模糊不清<br/> 
            2.不可有反光影响文字显示<br/>
            3.请让四个边角完整展示，不要用手遮挡证件<br/> 
            4.请不要自行加水印<br/>
            5.请不要使用扫描件或复印件
          </div>
          <div class="text3" v-if="errorType==12">{{errorTextMes}}</div>
          <div class="text1" v-if="errorType==3 || errorType==4">验证失败</div>
          <div class="text2" v-if="errorType==3 || errorType==4">请确保您的姓名、身份证号与人脸信息一致</div>
          <div class='text1' v-if="errorType==13">提交成功</div>
          <div class='text2' v-if="errorType==13">我们将尽快审核您的资料</div>
          <div v-if="errorType==9" class="text3">{{errorTextMes}}</div>
          <!--这里是新的-->
          <div v-if='noticeStatus==0 || noticeStatus==1 || noticeStatus==4' class="text1">识别失败</div>
          <div v-if="noticeStatus==0" class="text2 tl">
            1. 确保本人面部出现在框内<br/>
            2. 确保面部与屏幕保持适当距离<br/>
            3. 保持光鲜充足和均匀<br/>
            4. 摘掉眼镜并露出耳朵
          </div>
          <div v-if='noticeStatus==1' class="text2">请确保您的姓名、身份证号与手机号实名信息一致</div>
          <div class="text2" v-if="noticeStatus==4">如有疑问请联系客服</div>
          <div class="text2 text4" v-if="noticeStatus==4"><a href="tel:400-2222-222">400-2222-222</a></div>
        </div>
        <div class="flex font-bold toast-mybtn">
          <div @click.stop='isHideDialog' v-if="errorType==9" class="munual-btn1">返回</div>
          <div @click.stop='gotoVd' v-if="errorType==12 || errorType==3" class="munual-btn1">{{errorType==3 ? '再试一次':'我知道了'}}</div>
          <div @click.stop='errorShow=false;errorType=-1;' v-if="errorType==1 || errorType==2" class="munual-btn1">取消</div>
          <div @click.stop='addSgm(0)' v-if="errorType==11" class="munual-btn1">重新识别证件</div>
          <div v-if="errorType==9 || errorType==11 || errorType==1 || errorType==2" class="line"></div>
          <div @click.stop='addSgm(1)' v-if="errorType==11 || errorType==1 || errorType==2 || errorType==3 || errorType==4" class="munual-btn2">启用人工审核</div>
          <div @click.stop='gotoSuccess' v-if="errorType==13" class="munual-btn2">我知道了</div>
          <div @click.stop="loginPage" v-if="errorType==9" class="munual-btn2">切换帐号</div>
          <div @click.stop='closeDialog' v-if='noticeStatus==1' class="munual-btn1">再试一次</div>
          <div v-if='noticeStatus==1' class="line"></div>
          <div v-if='noticeStatus==1' @click.stop='gotoMaRouter("writeMes")' class="munual-btn2">启用人工审核</div>
          <div @click.stop='closeDialog'  v-if='noticeStatus==0' class="munual-btn2">重新识别</div>
          <div @click.stop='closePage' class="munual-btn2" v-if='noticeStatus==4'>我知道了</div>
        </div>
      </div>
    </div>
    <!--选择日期-->
    <DatetimePicker ref="date" @getDate="getDate"></DatetimePicker>
    <!--选择身份类型-->
    <Picker ref="Picker" @getPicker='getPicker'></Picker>
    <!--选择本地图片-->
    <uploadImage ref="uploadImage" @localImage='localImage' :uploadIndex="uploadIndex"></uploadImage>
  </div>
</template>

<script>
  import upload1 from '../../assets/img/certification/upload1.png';
  import upload2 from '../../assets/img/certification/upload2.png';
  import datePicker from 'mixins/datePicker.js'
  import pickerMixins from 'mixins/pickerMixins.js'
  import uploadImgMixins from 'mixins/uploadImgMixins.js'
  import inputLen from 'mixins/inputLen.js'
  import bridge from 'fetch/bridge.js'
  
  import {transferBuickFansFromUser} from 'fetch/api.js'
  
  let Base64 = require('js-base64').Base64
  export default{
    name:'faceIndex',
    mixins:[datePicker,pickerMixins,uploadImgMixins,inputLen],
    // components:{
    //   Picker,
    // },
    data(){
      return {
        upload1,
        upload2,
        // errorShow:false,
        /*
        证件识别失败-刷脸认证-OCR识别失败=>1
        */
        // errorType:1,
        uploadImg1:{url:'',res:false,localImg:''},
        uploadImg2:{url:'',res:false,localImg:''},
        isLong:false,//是否是长期证件
        uploadJson:{
          name:'',
          authType:1,
          authNumber:'',
          authPassType:2,
          authPassToken:'',
          imageId:'',
          frontImgUrl:'',
          backImgUrl:'',
          expireDate:'',
          holdImgUrl:'',
          gender:'',
          birth:'',
          address:'',
        },
        preventRes:false,
        errorNum:0,//累计提交错误次数
        type:'',
        noticeStatus:-1,
        faceErrorNum:0,
      }
    },
    deactivated(){
      sessionStorage.videoData='';
    },
    activated(){
      window.receiveFaceInfo=res=>{
        console.log('获取到数据',res)
        if(res){
          res=JSON.parse(res)
          console.log(res.status==1 , res.imageId)
          if(res.status==1 && res.imageId){
            sessionStorage.videoData=res.imageId;
            this.uploadJson.imageId=sessionStorage.videoData;//视频
            this.submitFn()
            //this.$router.go(-1);
          }else{
            sessionStorage.videoData='';
            this.isError();
          }
        }else{
          sessionStorage.videoData='';
          this.isError();
        }
      }
      
      console.log('当前页面==',this.$route)
      
      this.onGetImgKey=true;
      this.type=this.$route.query.type;
      this.tempCardName='居民身份证'
      //this.uploadJson.imageId=sessionStorage.videoData;//视频
      if(this.$route.query.test==2){
        this.uploadJson.imageId='6bf41b95a9d6473eb2cc5a39c0df014d';//测试用
      }
      //this.uploadJson.imageId='6bf41b95a9d6473eb2cc5a39c0df014d';//测试用
      
      let {img1,img2,postData,isLong}=this.$store.state.certCardMes;//三要素的图片信息
      console.log('获取到的图片信息=',{img1,img2,postData,isLong},'this.type=',this.type,'this.uploadJson.imageId=',this.uploadJson.imageId)
      if(img1){
        this.uploadImg1=img1;
        if(img1.name){
          this.uploadJson.name=img1.name;
        }
        if(img1.id_number){
          this.uploadJson.authNumber=img1.id_number;
        }
        if(img1.address){
          this.uploadJson.address=img1.address;
        }else{
          this.uploadJson.address='';
        }
      }else{
        this.uploadImg1={url:'',res:false,localImg:''};
      }
      if(img2){
        this.uploadImg2=img2
        if(this.uploadImg2.startTime){
          this.startTime=this.uploadImg2.startTime
        }
        if(this.uploadImg2.endTime){
          this.endTime=this.uploadImg2.endTime
        }
      }else{
        this.uploadImg2={url:'',res:false,localImg:''};
      }
      if(postData){
        this.uploadJson={...postData,authPassType:2,authType:1}
      }
      this.isLong=isLong ? isLong:false;
      if(this.$route.query.type!='app'){
        this.uploadJson.imageId=sessionStorage.videoData;//视频
        if(this.uploadJson.imageId){
          this.submitFn()
        }
      }
      
      // this.errorType=11;
      // this.errorShow=true;//测试
      // this.noticeStatus=1;
      
    },
    computed:{
      showUpload1(){
        return this.uploadImg1.res
      },
      showUpload2(){
        return this.uploadImg2.res
      },
      hasColBtn(){
        let {name,authNumber,address}=this.uploadJson
        let startTime=this.startTime;
        let isLong=this.isLong
        let endTime=this.endTime;
        // let checkSex=this.checkSex
        // let birthDay=this.birthDay;
        
        let res=name.length>=2 && authNumber && address && startTime && this.showUpload1 && this.showUpload2;
        console.log('验证颜色==',res,name.length>=2,!!authNumber,!!startTime,this.showUpload1,this.showUpload2)
        if(!this.isLong && this.tempCardType==0){
          return res && endTime;
        }else{
          return res
          // if(this.tempCardType!=0){
          //   console.log('111=',checkSex!=-1 , birthDay)
          //   return res && checkSex!=-1 && birthDay
          // }else{
          //   console.log('222=')
          //   return res
          // }
        }
      },
    },
    watch:{
      tempCardType:function(index){
        console.log('监听临时选择证件的类型==',index)
        if(index!=0){
          this.tempCardName='居民身份证';
          if(index==1){
            this.errorType=index
          }else{
            this.errorType=2
          }
          this.errorShow=true
        }
      },
    },
    methods:{
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      addSgm(num){
        let errorType=this.errorType;
        if(num==0){
          if(errorType==11){//重新识别证件
            this.authAction('my_realname_recheck')
          }
        }else if(num==1){
          if(errorType==11){//启用人工审核
            this.authAction('my_realname_artificial')
          }
          this.gotoRouter("writeMes");
        }
        if(this.errorShow){
          this.errorShow=false;
          this.errorType=-1;
        }
      },
      closePage(){
        //bridge('finishPage')
        if(this.$route.query.type=='app'){
          bridge('finishPage')
        }else{
          this.$router.go(-1);
          this.closeDialog();
        }
      },
      closeDialog(){
        this.errorShow=false;
        this.noticeStatus=-1;
        sessionStorage.videoData='';
        if(this.fromType==3){
          //this.gotoVd()
          bridge('scanFace');
        }
      },
      isError(){//报错
        this.$LoadingHide()
        this.errorType=-1;
        this.errorShow=true;
        if(this.fromType==3){
          if(this.faceErrorNum<3){
            this.noticeStatus=0;
          }else{
            this.noticeStatus=4;
          }
        }else{
          this.noticeStatus=1;
        }
        this.faceErrorNum++;
      },
      isHideDialog(){
        this.errorShow=false;
        this.errorType=-1;
        this.errorTextMes="";
        this.authAction('my_realname_submit_back')
      },
      loginPage(){
        this.authAction('my_realname_submit_change')
        bridge('closeAndGotoLoginPage')
      },
      clearData(){//清除数据
        this.isLong=false;
        this.startTime='';
        this.endTime='';
        sessionStorage.videoData='';
        let img={url:'',res:false,localImg:''}
        this.uploadImg1={...img}
        this.uploadImg2={...img}
        this.uploadJson={
          name:'',
          authType:1,
          authNumber:'',
          authPassType:2,
          authPassToken:'',
          imageId:'',
          frontImgUrl:'',
          backImgUrl:'',
          expireDate:'',
          holdImgUrl:'',
          gender:'',
          birth:'',
          address:'',
        }
        this.preventRes=false;
      },
      async startBtn(){
        let params={...this.uploadJson}
        if(!this.hasColBtn) return;
        let type=this.$route.query.type;
        if(type=='app'){
          bridge('scanFace');
        }else{
          this.$router.push({name:'face',query:{type}})
        }
        let data={}
        // uploadJson:{
        //   name:'',
        //   authType:1,
        //   authNumber:'',
        //   authPassType:2,
        //   authPassToken:'',
        //   imageId:'',
        //   frontImgUrl:'',
        //   backImgUrl:'',
        //   expireDate:'',
        //   holdImgUrl:'',
        //   gender:'',
        //   birth:'',
        // },
        if(this.uploadImg1 && this.uploadImg1.url){
          data.img1=this.uploadImg1
          data.img1.name=this.uploadJson.name;
          data.img1.id_number=this.uploadJson.authNumber;
          data.img1.address=this.uploadJson.address;
        }
        if(this.uploadImg2 && this.uploadImg2.url){
          data.img2=this.uploadImg2
        }
        this.preventRes=false;
        this.$store.commit('SET_CERTCARDMES',{})
        this.$store.commit('SET_CERTCARDMES',{...data,postData:this.uploadJson,isLong:this.isLong})
      },
      async submitFn(){//提交
        let params={...this.uploadJson}
        if(this.preventRes) return;
        this.preventRes=true
        this.$LoadingShow('加载中')
        let time='';
        console.log('this.startTime===',this.startTime)
        if(!this.isLong){
          time=this.startTime.replace(/-/g,'.')+'-'+this.endTime.replace(/-/g,'.')
        }else{
          time=this.startTime.replace(/-/g,'.')+'-长期'
        }
        params.name=Base64.encode(params.name)
        params.authNumber=Base64.encode(params.authNumber)
        params.address=Base64.encode(params.address)
        params.expireDate=time
        // params.gender=parseFloat(this.checkSex)+1;
        // params.birth=this.birthDay
        params.frontImgUrl=this.uploadImg1.url
        //params.holdImgUrl=params.frontImgUrl;
        params.backImgUrl=this.uploadImg2.url
        params.authType=this.getCardType(this.tempCardName);
        if(params.authType==0){
          params.authType=1;
        }
        console.log('this.tempCardName==',this.tempCardName,params.authType,'address=',this.uploadJson.address)
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'userAuth/transferBuickFansFromUser',gateway:"private",params:params,method:"POST"
          },
          {
            callback:this.transferBuickFansFromUserCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await transferBuickFansFromUser(params)
          this.transferBuickFansFromUserCall(res)
        }
      },
      transferBuickFansFromUserCall(res){
        this.$LoadingHide()
        this.preventRes=false;
        console.log('res=',res)
        let code=res.resultCode
        if(res && code=='0000'){
          // this.errorShow=true;
          // this.errorType=13;
          this.gotoSuccess();
        }else{
          sessionStorage.videoData='';
          this.errorShow=true;
          if(code=='1150'){
            this.errorNum=this.errorNum+1;//提交错误加一
            if(this.errorNum<3){//点提交返回1150错，即三要素识别不通过，弹框（三要素认证不通过 <3）
              this.errorType=3;
            }else{//点提交返回1150错，满3次，弹框（三要素认证-失败>=3次）
              this.errorType=4;
              this.authAction('my_realname_submit_goface')
            }
          }else if(code=='1151'){//证件号已被认证
            if(this.$route.query.type=='app'){
              this.errorType=9;
            }else{
              this.errorType=12;
            }
            
            this.errorTextMes=res.message;//其他错误提示
          }else{
            this.errorType=12;
            this.errorTextMes=res.message;//其他错误提示
          }
        }
      },
      gotoSuccess(){
        if(this.$route.query.type=='app'){
          bridge('JoinSuccess')
        }
        let clientType=sessionStorage.isminiType;
        this.$router.replace({name:'faceSuccess',query:{type:this.$route.query.type,clientType}})
      },
      getCardType(name){
        switch(name){
          case '居民身份证':
            return 0;
          case '护照':
            return 4;
          case '台湾居民往来通行证':
            return 3;
          case '港澳居民往来通行证':
            return 2;
        }
      },
      getUploadMes(res,uploadIndex,tempCarType){//上传证件文件
        console.log('上传证件文件0000=',res,uploadIndex,'tempCarType=',tempCarType)
        if(res.resultCode=='0000'){
          let {key,data}=res.data;
          if(uploadIndex==1){
            let mes=JSON.parse(Base64.decode(data));
            let {name,id_number,address}=mes;
            this.uploadImg1.res=true;
            this.uploadImg1.name=name;
            this.uploadImg1.id_number=id_number;
            this.uploadImg1.address=address;
            this.uploadImg1.url=key;
            if(!(name && id_number)){
              this.errorShow=true;
              this.errorType=11;
            }
            this.uploadJson.name=name;
            this.uploadJson.authNumber=id_number;
            this.uploadJson.address=address;
          }else if(uploadIndex==2){
            if(tempCarType==0){
              let mes=JSON.parse(Base64.decode(data));
              let {validity}=mes
              this.uploadImg2.validity=validity;
              if(validity){
                let time=validity.split("-");
                this.startTime=time[0].replace(/\./g,'-');
                this.endTime=time[1].replace(/\./g,'-');
                this.uploadImg2.startTime=this.startTime
                this.uploadImg2.endTime=this.endTime
              }else{
                this.uploadImg2.startTime='';
                this.uploadImg2.endTime='';
                this.startTime='';
                this.endTime='';
                this.errorShow=true;
                this.errorType=11;
              }
            }
            this.uploadImg2.res=true;
            this.uploadImg2.url=key
          }
        }else{
          let img=this.uploadImg1;
          this.errorShow=true;
          this.errorType=11;
          if(uploadIndex==2){
            img=this.uploadImg2;
            //this.errorType=11;
            this.uploadImg2.startTime='';
            this.uploadImg2.endTime='';
            this.uploadImg2.validity='';
            this.startTime='';
            this.endTime='';
            
          }else{
            this.uploadJson.name='';
            this.uploadJson.authNumber='';
            this.uploadJson.address='';
            this.uploadImg1.name='';
            this.uploadImg1.id_number='';
            this.uploadImg1.address='';
            //this.errorType=12;
            this.errorTextMes=res.message;//其他错误提示
          }
          img.url=''
          img.res=false;
        }
      },
      gotoMaRouter(name){//跳转到人工审核
        console.log('跳转到人工审核')
        this.$router.push({name,query:{typeName:encodeURIComponent('居民身份证'),type:this.$route.query.type}})
        this.closeDialog();
      },
      gotoRouter(name){//跳转到人工审核
        let typeName='居民身份证';
        if(this.errorType==1){
          typeName='护照';
        }else if(this.errorType==2){
          typeName='台湾居民往来通行证'
          if(this.tempCardType!=2){
            typeName='港澳居民往来通行证'
          }
        }
        this.$store.commit('SET_CERTCARDMES',{})
        console.log('这里是什么身份=',typeName)
        if(typeName=='居民身份证'){
          let data={}
          if(this.uploadImg1 && this.uploadImg1.url){
            data.img1=this.uploadImg1
          }
          if(this.uploadImg2 && this.uploadImg2.url){
            data.img2=this.uploadImg2
          }
          this.$store.commit('SET_CERTCARDMES',{...data,postData:this.uploadJson,isLong:this.isLong})
        }
        console.log('跳转到人工审核==name=',name)
        this.$router.push({name,query:{typeName:encodeURIComponent(typeName),type:this.$route.query.type}})
        this.errorShow=false;
        this.tempCardType=0
      },
      gotoVd(){
        if(this.errorType==3){
          this.startBtn()
        }
        this.errorShow=false;
        this.errorType=-1;
      },
    }
  }
</script>

